<template>
  <div>
    <template v-if="loading">
      <div class="loading">
        <Loader/>
      </div>
    </template>
    <template v-else>
      <section class="main-grid__row user-profile">
        <div class="user-profile__card">
          <div class="user-profile__primary">
            <p class="user-profile__name">{{ itemData.title }}</p>
          </div>
        </div>

        <div class="user-profile__tabs">
          <ul class="nav nav-tabs" id="myTab" role="tablist">
            <li class="nav-item">
              <a
                class="nav-link active"
                id="edit-class-tab"
                data-toggle="tab"
                href="#edit-class"
                role="tab"
                aria-controls="edit-class"
                aria-selected="false"
              >Edit Lesson</a>
            </li>
            <li class="nav-item">
              <a
                class="nav-link"
                id="quiz-tab"
                data-toggle="tab"
                href="#quiz"
                role="tab"
                aria-controls="quiz"
                aria-selected="false"
              >Modules</a>
            </li>
          </ul>
          <div class="tab-content" id="myTabContent">
            <div
              class="tab-pane fade show active user-profile__tabs__section"
              id="edit-class"
              role="tabpanel"
              aria-labelledby="edit-class-tab"
            >
              <div class="user-profile__notification">
                <div class="user-profile__notification__item">
                  <div>
                    <form method="post" @submit.prevent="updateLesson">
                      <div class="settings__field">
                        <div class="form-row">
                          <div class="form-group col-md-8">
                            <div class="settings__value">
                              <label for="jobTitle">Title</label>
                              <input
                                :class="[{'is-invalid': errors.title}]"
                                @keydown="clearErrors($event.target.name)"
                                v-model="itemData.title"
                                name="title"
                                value
                                type="text"
                                class="form-control"
                                id="jobTitle"
                              >
                            </div>
                            <div class="invalid-feedback">{{ errors.title }}</div>
                          </div>
                        </div>
                        <div class="form-row">
                          <div class="form-group">
                            <div class="settings__value">
                              <editor
                                :init="init"
                                ref="myTextEditor"
                                v-model="itemData.description"
                                :options="editorOption"
                              ></editor>
                            </div>
                            <div class="invalid-feedback">{{ errors.description }}</div>
                          </div>
                        </div>
                      </div>
                      <div class="settings__primary">
                        <button
                          class="btn btn__save"
                          type="button"
                          name="button"
                          @click.prevent="updateLesson"
                          v-bind="{'disabled': loading}"
                        >Save</button>
                        <Loader v-if="loading"/>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>

            <div
              class="tab-pane fade user-profile__tabs__section"
              id="quiz"
              role="tabpanel"
              aria-labelledby="quiz-tab"
            >
              <div class>
                <div>
                  <div class="main__header">
                    <div class="main__primary">
                      <h4 class="main__title">Modules</h4>
                    </div>

                    <div class="d-flex">
                      <button
                        class="btn btn__primary"
                        type="button"
                        name="button"
                        data-toggle="modal"
                        data-target="#moduleModal"
                      >
                        <span class="btn__icon">
                          <svg
                            width="24"
                            height="13"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path
                              d="M8 5H5V2H3v3H0v2h3v3h2V7h3V5zm10 1c1.66 0 2.99-1.34 2.99-3S19.66 0 18 0c-.32 0-.63.05-.91.14.57.81.9 1.79.9 2.86 0 1.07-.34 2.04-.9 2.86.28.09.59.14.91.14zm-5 0c1.66 0 2.99-1.34 2.99-3S14.66 0 13 0c-1.66 0-3 1.34-3 3s1.34 3 3 3zm6.62 2.16C20.45 8.89 21 9.82 21 11v2h3v-2c0-1.54-2.37-2.49-4.38-2.84zM13 8c-2 0-6 1-6 3v2h12v-2c0-2-4-3-6-3z"
                              fill="#fff"
                            ></path>
                          </svg>
                        </span>Add Module
                      </button>
                      <AddModule/>
                    </div>
                  </div>
                  <section class="main-grid__row contacts contacts--list">
                    <div class="contacts--list__wrapper">
                      <div class="contacts--list__item head module">
                        <p>Title</p>
                        <p>Action</p>
                        <p>Description</p>
                      </div>
                      <div class="contacts__body">
                        <template v-if="modules.length">
                          <div
                            v-for="module in modules"
                            :key="module.id"
                            :lesson="module"
                            class="contacts--list__item module"
                          >
                            <p class="info__email">
                              <router-link
                                :to="{ name: 'module-detail',
																params: {
																	moduleId: module.id,
																	lessonId: $route.params.lessonId,
																	classId: $route.params.classId
															}}"
                              >{{ module.title }}</router-link>
                            </p>
                            <p v-html="module.content"></p>
                            <p class="btn__row">
                              <button
                                class="btn btn__danger"
                                @click="deleteClassModule(module)"
                              >Delete</button>
                              <router-link
                                :to="{ name: 'module-detail',
																params: {
																	moduleId: module.id,
																	lessonId: $route.params.lessonId,
																	classId: $route.params.classId }}"
                                class="btn btn__primary"
                              >View</router-link>
                            </p>
                          </div>
                        </template>
                        <template v-else>
                          <p>No lesson created yet</p>
                        </template>
                      </div>
                    </div>
                  </section>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </template>
  </div>
</template>

<script>
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import { quillEditor } from "vue-quill-editor";
import Loader from "../../components/Loader";
import eventHub from "../../utils/eventhub";
import form from "../../mixins/form";
import { success, fail } from "../../utils/toast";
import classes from "../../utils/validations/classes";
import {
  updateLesson,
  getLesson,
  deleteClassModule
} from "../../utils/api/classes";
import AddModule from "./AddModule";
import init from "../../config/tinymce";

export default {
  name: "LessonDetail",
  mixins: [form],
  data() {
    return {
      init,
      loading: true,
      itemData: {},
      errors: {},
      modules: [],
      editorOption: {
        modules: {
          toolbar: [
            ["bold", "italic", "underline", "strike"],
            ["blockquote", "code-block"],
            [{ header: 1 }, { header: 2 }],
            [{ list: "ordered" }, { list: "bullet" }],
            [{ script: "sub" }, { script: "super" }],
            [{ indent: "-1" }, { indent: "+1" }],
            [{ direction: "rtl" }],
            [{ size: ["small", false, "large", "huge"] }],
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ font: [] }],
            [{ color: [] }, { background: [] }],
            [{ align: [] }],
            ["clean"],
            ["link", "image", "video"]
          ]
        }
      }
    };
  },
  components: {
    Loader,
    AddModule,
    quillEditor
  },
  created() {
    this.getLesson();
    eventHub.$on("new-module", payload => {
      this.modules.unshift(payload);
    });
  },
  methods: {
    getLesson() {
      getLesson(this.$route.params.classId, this.$route.params.lessonId)
        .then(response => {
          if (response.status === 200) {
            this.itemData = response.data.data;
            this.modules = response.data.data.modules;
            this.loading = false;
          }
        })
        .catch(error => {
          this.loading = false;
          return error;
        });
      return true;
    },
    updateLesson() {
      const { description, title } = this.itemData;
      const result = { description, title };
      this.validate(result, classes.lesson);
      if (Object.keys(this.issues).length) {
        this.errors = this.issues;
        return false;
      }
      const { classId, lessonId } = this.$route.params;

      updateLesson(classId, lessonId, result)
        .then(response => {
          if (response.status === 200) {
            this.loading = false;
            success({
              title: "Success! Lesson has been updated!"
            });
          }
        })
        .catch(error => {
          this.loading = false;
          fail({
            title: "Ooops! Something went wrong. Pls try again."
          });
          return error;
        });
      return true;
    },
    deleteClassModule(item) {
      const { classId, lessonId } = this.$route.params;
      deleteClassModule(classId, lessonId, item.id)
        .then(response => {
          if (response.status === 200) {
            success({
              title: "Success! Module has been deleted!"
            });
            this.modules.splice(this.modules.indexOf(item), 1);
            this.loading = false;
          }
        })
        .catch(error => {
          fail({
            title: "Oops! Something went wrong",
            text: error.data
          });
          this.loading = false;
          return error;
        });
      return true;
    }
  },
  computed: {
    editor() {
      return this.$refs.myTextEditor.quill;
    }
  }
};
</script>


